@import "./sass-variables.sass";

#footer {
    box-sizing: border-box;
    min-height: 400px;
    background: $lin2;
    background-size: $bs1;
    background-position: center;
    color: $clight;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    overflow: hidden;
    #mainFooter {
        display: flex;
        width: 100%;
        height: 400px;
        margin: auto;
        justify-content: center;
        #footerPhoto {
            // border: 5px solid white
            width: 400px;
            img {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translateX(-63%) translateY(-48%);
                height: 108%;
                width: auto; } }
        #footerCopyrightsContainer {
            // border: 5px solid peru
            display: flex;
            flex-direction: column;
            // justify-content: space-between
            #contactSocialsContainer {
                display: flex;
                #contactFootContainer {
                    // border: 5px solid yellow
                    // width: 400px
                    min-width: 225px;
                    h1 {
                        font-size: 36px;
                        padding-top: 90px;
                        padding-bottom: 30px;
                        margin: 0; }
                    p {
                        margin-bottom: 20px;
                        .icon {}
                        a {
                            text-decoration: none;
                            color: $clight;
                            &:hover {
                                color: $cthird; } } } }
                #meSocialsContainer {
                    // border: 5px solid red
                    display: flex;
                    // width: 300px
                    height: 300px;
                    display: flex;
                    align-items: center;
                    margin-left: 0px;
                    transform: translateX(100px);
                    .navIcon {
                        width: 50px;
                        height: 50px;
                        margin: 0;
                        padding: 0;
                        // border: 5px solid lavender
                        margin-left: 45px;
                        a {
                            color: $clight;
                            &:hover {
                                color: $cthird; }
                            .icon {
                                margin: 0;
                                padding: 0; } } } } }

            #copyrights {
                // border: 5px solid lawngreen
                height: 50px;
                display: flex;
                p {
                    width: 100%;
                    max-width: $maxWidth;
                    color: $cthird; } } } } }

@media(max-width: 1200px) {
    #footer {
        #mainFooter {
            #footerCopyrightsContainer {
                #contactSocialsContainer {
                    #meSocialsContainer {
                        margin-top: 50px;
                        flex-direction: column;
                        height: 300px;
                        transform: translateX(0px);
                        .navIcon {
                            // border: 5px solid lavender
                            margin-left: 45px;
                            margin-top: 20px; } } } } } } }

@media(max-width: 900px) {
    #footer {
        #mainFooter {
            #footerCopyrightsContainer {
                #contactSocialsContainer {
                    flex-direction: column;
                    #meSocialsContainer {
                        margin-top: 20px;
                        margin-bottom: 30px;
                        flex-direction: row;
                        // height: 300px
                        transform: translateX(0px);
                        height: 50px;
                        .navIcon {
                            // border: 5px solid lavender
                            margin-left: 45px;
                            margin-top: 20px;
                            margin-left: 0px;
                            margin-right: 45px; } } } }
            #footerPhoto {
                width: 400px;
                img {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translateX(-63%) translateY(-30%);
                    height: 90%;
                    width: auto; } } } } }
@media(max-width: 600px) {
    #footer {
        height: auto;
        #mainFooter {
            flex-direction: column;
            #footerCopyrightsContainer {
                margin-left: 30px;
                margin-top: 330px;
                margin-bottom: 20px;
                #contactSocialsContainer {
                    flex-direction: column;
                    #meSocialsContainer {
                        margin-top: 20px;
                        margin-bottom: 30px;
                        flex-direction: row;
                        // height: 300px
                        transform: translateX(0px);
                        height: 50px;
                        .navIcon {
                            // border: 5px solid lavender
                            margin-left: 45px;
                            margin-top: 20px;
                            margin-left: 0px;
                            margin-right: 45px; } } } }

            #footerPhoto {
                margin: auto;
                // width: 400px
                height: 400px;
                order: 1;
                img {
                    position: relative;
                    left: 0%;
                    top: 0%;
                    transform: translateX(5%) translateY(-80%);
                    height: 100%;
                    width: auto; } } } } }

@media(max-width: 330px) {
    #footer {
        height: auto;
        #mainFooter {
            flex-direction: column;
            #footerCopyrightsContainer {
                margin-left: 30px;
                margin-top: 0px;
                margin-bottom: 20px;
                #contactSocialsContainer {
                    flex-direction: column;
                    #meSocialsContainer {
                        margin-top: 20px;
                        margin-bottom: 30px;
                        flex-direction: row;
                        // height: 300px
                        transform: translateX(0px);
                        height: 50px;
                        .navIcon {
                            // border: 5px solid lavender
                            margin-left: 45px;
                            margin-top: 20px;
                            margin-left: 0px;
                            margin-right: 45px; } } } }

            #footerPhoto {
                display: none;
                margin: auto;
                // width: 400px
                height: 400px;
                order: 1;
                img {
                    position: relative;
                    left: 0%;
                    top: 0%;
                    transform: translateX(0%) translateY(0%);
                    height: 100%;
                    width: auto; } } } } }
